}
.signup-form {
    width: 425px;
    margin: 8px auto;
    padding: 30px 0;
    

}
.signup-form h2 .btn-primary{
    color: #333;
    font-weight: bold;
    margin: 0 0 25px;
   
}
.signup-form form {
    
    margin-bottom: 15px;
    background: #fff;
    border: 1px solid #f4f4f4;
    box-shadow: 0 12px 40px rgba(0,0,0,.12);
    padding: 40px 50px;
    caret-color: rgb(98, 154, 98);
}
.signup-form .form-group {
    margin-bottom: 20px;
}
.signup-form label {
    font-weight: normal;
    font-size: 13px;
}
.signup-form input[type="checkbox"] {
    margin-top: 2px;
}    
.signup-form .btn {        
    font-size: 16px;
    font-weight: bold;
    background:rgb(98, 154, 98);
    border: none;
    min-width: 140px;
    outline: none !important;
}
.signup-form .btn:hover .signup-form .btn:focus{
    background: rgb(98, 154, 98);
}
